<script>
export default {
  data() {
    return {
      e6: 1,
      alert: false,
      alertinfo: '',
      select: { state: "文献", abbr: "text" },
      items: [
        { state: "文献", abbr: "text" },
        { state: "视频", abbr: "video" },
