        { state: "传统网站", abbr: "normal" },
        { state: "JS异步型", abbr: "js" }
      ],
      //
      dialog: false,
      editdialog: false,
      info: "",
      form: {},
      result: "",
      search: "",
      headers: [
        {
          text: "项目名称",
          align: "left",
          sortable: false,
          value: "name"
        },
        { text: "域名", value: "domain", align: "left" },
        { text: "资源类型", value: "type", align: "left" },
        { text: "状态", value: "status", align: "left" },
        { text: "已爬取链接数", value: "linknum", align: "left" },
        { text: "操作", value: "operate", align: "left" }
      ],
      desserts: [],
      deletedialog: false,
      spinfo: {}
    };
  },
  created() {
    this.loadtabledata();
  },
  methods: {
    clear() {
      this.$refs.form.reset()
      this.e6 = 1
      this.dialog = false
    },
    onSubmit() {
      // console.log(this.info)
      // console.log(this.form.pages2)
      console.log(this.$refs.form.validate())

      axios
        .post("http://192.168.159.129:5000/admin", {
          name: this.form.name,
          domain: this.form.domain,
          rule: this.form.rule,
          pages: this.form.pages,
          select_title: this.form.select_title,
          select_url: this.form.select_url,
          type: this.form.type.abbr,
          type2: this.form.type2.abbr
        })
        .then(res => {
          this.result = res.data;
          this.loadtabledata();
        })
        .catch(err => {
          console.log("err:" + err);
          // 异常情况
        });
      this.clear()
    },
    gettitle() {
      axios
        .post("http://192.168.159.129:5000/gettitle", {
            'select_title': this.form.select_title,
            'select_url': this.form.select_url
          })
          .then(res => {
            this.alertinfo = res.data;
            this.alert = true;
          })
          .catch(err => {
            console.log("err:" + err);
          });
    },
    handleEdit(item) {
      axios
        .post("http://192.168.159.129:5000/getbaseinfo", {
          uuid: item.uuid
        })
        .then(res => {
          this.spinfo = res.data
          // this.loadtabledata();
          this.editdialog = true
        })
        .catch(err => {
          console.log("err:" + err);
          // 异常情况
        });
      // this.editdialog = true
    },
    updatebaseinfo(item) {
      axios
        .post("http://192.168.159.129:5000/updatebaseinfo", this.spinfo)
        .then(res => {
          // this.spinfo = res.data
          console.log(res.data)
          this.loadtabledata();
          this.editdialog = false
        })
        .catch(err => {
          console.log("err:" + err);
          // 异常情况
        });
      // this.editdialog = true
    },
    handleDelete(item) {
      console.log(item.uuid);
      axios
        .post("http://192.168.159.129:5000/taskdel", {
          uuid: item.uuid
        })
        .then(res => {
          this.loadtabledata();
        })
        .catch(err => {
          console.log("err:" + err);
          // 异常情况
        });
    },
    loadtabledata() {
      axios
        .get("http://192.168.159.129:5000/taskget")
        .then(res => {
          this.desserts = res.data;
        })
        .catch(err => {
          console.log("err:" + err);
          // 异常情况
        });
    }
  }
};
</script>
