<script>
export default {
  data() {
    return {
      id: 0,
      items: [],
      chatcontent: ""
    };
  },
  created() {
    this.loadtabledata();
  },
  mounted() {
    setInterval(() => {
      this.loadtabledata();
    }, 1200);
  },
  methods: {
    chat() {
      this.state = true;
      // console.log(this.info)
      axios
        .post("/send_bbb", {
          test0: this.chatcontent
        })
        .then(res => {
          //   this.loadtabledata()
          this.chatcontent = "";
        })
        .catch(err => {
          console.log("err:" + err);
          // 异常情况
        });
    },
    reset() {
      this.chatcontent=''
    },
    initdata() {
      axios
        .get("/init")
        .then(res => {
          //   this.loadtabledata()
          // this.chatcontent = "";
          console.log(res)
        })
        .catch(err => {
          console.log("err:" + err);
          // 异常情况
        });
    },
    loadtabledata() {
      axios
        .post("/chat_get", { id: this.id })
        .then(res => {
          this.items = res.data;
        })
        .catch(err => {
          console.log("err:" + err);
          // 异常情况
        });
    }
  }
};
